$brk-base-colors: ();
$brk-base-colors: map-merge((
				"brand-primary":      adjust-hue(#2775FF, 120deg),
				"secondary":          adjust-hue(#7202bb, 120deg),
				"white":              #fff,
				"black":              #000,
				"brk-dark-base":      adjust-hue(#272727, 120deg),
				"brk-base-1":         adjust-hue(#69adff, 120deg),
				"brk-base-2":         adjust-hue(#00c6ff, 120deg),
				"brk-base-3":         adjust-hue(#0F5AE0, 120deg),
				"brk-base-4":         adjust-hue(#e8ecff, 120deg),
				"brk-base-5":         adjust-hue(#00f6ff, 120deg),
				"brk-base-6":         adjust-hue(#0B98F6, 120deg),
				"brk-base-7":         adjust-hue(#a7ebff, 120deg),
				"brk-base-8":         adjust-hue(#1600ba, 120deg),// TODO: !!!
				"brk-secondary-1":    adjust-hue(#4f34c9, 120deg),
				"brk-secondary-2":    adjust-hue(#4F00BA, 120deg),
				"brk-secondary-3":    adjust-hue(#7400ba, 120deg),
				"brk-secondary-4":    adjust-hue(#6e50c2, 120deg),
				"brk-secondary-5":    adjust-hue(#6377DF, 120deg),
				"brk-secondary-6":    adjust-hue(#b50aff, 120deg),
				"text-base-color-1":  #c4c4c4,
				"text-base-color-2":  adjust-hue(#0F5AE0, 120deg),
				"text-base-color-3":  #f3f3f3,
				"form-color-1":					adjust-hue(#e8ecff, 120deg),
), $brk-base-colors);

@function safe-rgb($color) {
	@return unquote("#{red($color)}, #{green($color)}, #{blue($color)}");
}

/* ----------- Palette variable ----------- */
:root {
	@each $color, $value in $brk-base-colors {
		--#{$color}: #{$value};
	}

	// RGB
	@each $color, $value in $brk-base-colors {
		--#{$color}-rgb: #{safe-rgb($value)};
	}

	--b-radius: inherit;
}
/* ----------- Palette variable ----------- */
@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.overlay__delimiter {
	opacity: 0.9
}

[class*="delimiter"]{
	figure{
		box-shadow: none;
	}
}

.delimiter {
	&:before,
	&:after {
		content: '';
		width: 100%;
		height: 100%;
	}

	&__circle {
		.services__wrapper-honeycomb .services__wrapper .icon__cover {
			margin-bottom: 30px;
		}

		.services__wrapper-main {
			z-index: 6;
			position: relative;
		}

		.overlay__delimiter {
			&-before {
				left: 0;
				top: -1px;

				svg {
					bottom: auto;
				}
			}
		}
	}

	&__angle {
		padding-bottom: 13.5vw;

		@media #{$xs-max} {
			padding-bottom: 60px;
		}

		.overlay__delimiter {
			&-before {
				left: 0;
				bottom: -2px;
			}
		}

		.icon__btn{
			.icon {
				width: 50%;
			}

			.livicon-evo-holder{ 
				bottom: auto;
				right: auto;
				width: 70% !important;
				top: 50%;
				left: 50%;
        transform: translate(-50%, -50%);
			}
		}
	}

	&__curve {
		padding-bottom: 5vw;

		@media (max-width: 992px) {
			padding-bottom: 120px !important;
		}

		.highlight-underline {
			margin-top: 20px;
		}

		.overlay__delimiter-before {
			left: 0;
			bottom: -2px;
		}
	}

	&__trapezoid {
		.overlay__delimiter {
			&-before {
				left: 0;
				bottom: 0;
				top: -1px;
				//height: 100%;
				width: auto;

				svg {
					bottom: auto;
					height: 100%;
					width: 100%;
				}
			}
		}
	}

	&__corner {
		h1,h2 {
			opacity: .1;
			color: $white;

			@media #{$xs-max} {
				line-height: 25px;
				font-size: 3.4rem;
			}
		}

		.overlay__delimiter {
			&-before {
				left: 0;
				bottom: -2px;
			}
		}

		& * {
			z-index: 5;
			position: relative;
		}
	}

	&__rounded {
		.overlay__delimiter {
			&-after {
				z-index: -1;
			}
			&-before {
				left: 0;
				bottom: -1px;
				z-index: -1;
			}
		}

		.all-light {
			@media #{$xs-max} {
				margin-bottom: 30px;
			}
		}

		p {
			color: #bce1ff;
		}
		.border-btn {
			border-color: var(--brk-base-2) !important;
		}
		.highlight-underline > .before {
			background: var(--brk-base-2) !important;
		}
	}
}

.overlay__delimiter {
	&-before {
		display: block;
		position: absolute;
		left: 0;
		right: 0;
		background-repeat: no-repeat;
		z-index: 5;

		@media #{$xs-max} {
			display: none;
		}

		svg {
			position: relative;
			bottom: -6px;
		}
	}

	&-after {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 3;
	}
}

.container-xl {
	@media (min-width: 1700px) {
		width: 1700px;
		max-width: 1700px;
	}
}